body {
  margin: 0;
}

.product-card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  display: flex;
}

.product-card__image {
  box-sizing: border-box;
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 160px;
  height: 134px;
  margin: 32px 19px 0;
}

.product-card__content {
  margin: 40px 16px 16px;
  font-family: Roboto, sans-serif;
}

.product-name {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-rating {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin: 16px 0 0;
  display: flex;
}

.product-rating__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-price {
  flex-direction: row;
  justify-content: space-between;
  margin: 24px 0 0;
  display: flex;
}

.product-price__text {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-price__value {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.buy-button {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 16px 0 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  flex-direction: row;
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:last-of-type {
  background-image: url("star.822d561e.svg");
}
